<template>
    <div class="games-container">

        <div :class="{'active':show}" ref="notification">
            <a :href="gameTo.url" target=”_blank” rel="noopener noreferrer">
                <div class="notification">
                    <p>Click here to check the game cards.</p>
                    <p>{{gameTo.game}} on {{gameTo.platform}}</p>
