        <div :class="{'loading':isLoading}" class="active-games">

            <div class="games">

                <!-- banner -->
                <div v-tilt="{speed: 800, perspective: 1000, max: 2, scale: 1.05}" class="banner">
                    <p>Active - {{ games.length}}
                        {{games.active && games.active.length == 1 ? 'game' : 'games'}}</p>
                    <!-- <p>Active</p> -->
                </div>

                <!-- games -->
                <div>
                    <!-- // game container -->
                    <div v-tilt="{speed: 100, perspective: 1000, max: 5, scale: 1.05, glare: true,'max-glare': .5, easing: `cubic-bezier(.03,.98,.52,.99)`}"
                        class="game-container game-container--active" v-for="(game, index) in games"
                        :key="game.ID + index" @click="visit({
                        botStock: game.data.status.botStock,
                        cards: game.data.status.cards,
                        header_image: game.data.header_image
                    })">
                        <!-- // game platform -- top position -->
                        <div v-tilt="{speed: 800, perspective: 1500, scale: 1.05, glare: true, max: 2, easing: `cubic-bezier(.03,.98,.52,.99)`}"
                            class="game-platform">
                            <div class="game-platform-ct game-platform-ct--active" style="display: flex;">
                                <img class="game-platform-avatar" :src="getPlatformAvatar('steam')" alt="">
                                <p class="game-platform-name">{{game.ID}}</p>
                            </div>
                            <p class="game-platform-from-time">{{ game.data.status.cards + ' cards to craft a badge.'}}</p>
                        </div>


                        <div class="cont-1">


                        <!-- // game avatar and time left -- right position -->
                        <div v-tilt="{speed: 800, perspective: 1000, max: 5, scale: 1.05, glare: true, easing: `cubic-bezier(.03,.98,.52,.99)`}"
                            class="game-avatar-ct">
                            <img class="game-avatar-img game-avatar-img--active" :src="game.data.header_image" alt="">
                        </div>

                        <!-- // game informations -- left position -->
                        <div v-tilt="{speed: 100, perspective: 1200, max: 5, scale: 1.08, glare: true, easing: `cubic-bezier(.03,.98,.52,.99)`}"
                            class="game-info game-info--active">
                            <p class="game-info-name">{{game.data.status.name + 's'}}</p>
                            <!-- <p>-------------------------------------------------------</p> -->
                                <!-- <p>{{game.data.status.botStock + ' available sets.'}}</p> -->
                        </div>

                        </div>
                    </div>
