</template>

<script>
    import Loading from 'vue-loading-overlay';
    // Import stylesheet
    import 'vue-loading-overlay/dist/vue-loading.css';

    import SteamLogo from '../assets/logo/steam.png'
    import GOGLogo from '../assets/logo/gog.png'
    import EpicGamesLogo from '../assets/logo/epic.png'

    export default {
        name: 'Games',
        components: {
            Loading
        },
        data() {
            return {
                games: {},
                gameTo: {
                    game: '',
                    platform: '',
                    url: ''
                },
                show: false,

                isLoading: false,

                showLeft: null,
            }
        },
        methods: {
            getPlatformAvatar(platform) {
                let p = platform.toLowerCase()
                if (p == 'steam') return SteamLogo
                if (p == 'gog') return GOGLogo
                if (p == 'epic games') return EpicGamesLogo
            },
            getRating(r) {
                let rate = Number(r)
                if (rate >= 95) return 'Overhwelmingly Positive'
                if (rate >= 80) return 'Very Positive'
                if (rate >= 70) return 'Mostly Positive'
                if (rate >= 40) return 'Mixed'
                if (rate >= 20) return 'Mostly Negative'
                if (rate >= 10) return 'Very Negative'
                if (rate >= 0) return 'Overwhelmingly Negative'

            },
            getColor(c) {
                if (c >= 80) return 'positive'
                if (c >= 50) return 'mixed'
                if (c >= 0) return 'negative'
            },
            visit(game) {
                // this.$refs.notification.
                this.show = true
                this.gameTo.game = game.name
                this.gameTo.url = game.url
                this.gameTo.platform = game.platform


                clearTimeout(this.showLeft)
                this.showLeft = setTimeout(() => this.show = false, 5000)

            },
            async getGames() {
                this.isLoading = true
                const res = await fetch(this.$store.state.gameApi)
                this.games = await res.json()
                this.isLoading = false
            }
        },
        mounted() {
            this.getGames()
        }
    }
</script>

