    <!-- <h1 style="text-align: center; padding-top: 1em; font-size: 1.2em;">Calculate your Steam Level</h1> -->
    <div style="padding-top: 1.2em;" class="levelup">
        <input min="0" max="5000" v-model="input.level"  @change="updateRate()" v-tilt="{speed: 800, perspective: 1500, scale: 1.05, glare: true, max: 2, easing: `cubic-bezier(.03,.98,.52,.99)`}" class="login__input login--left" type="number" placeholder="Your Steam Level" name="" id="">
        <input min="0" max="5000" v-model="input.desired" @change="updateRate()" v-tilt="{speed: 800, perspective: 1500, scale: 1.05, glare: true, max: 2, easing: `cubic-bezier(.03,.98,.52,.99)`}" class="login__input" type="number" placeholder="Desired Level" name="" id="">
    <div>

  </div>

 </div>

    <div class="nav-right ">
        <div class="nav--grab">
        
          <div class="text--trade">
            <p class="text--trade-name">TF2 Keys</p>
            <p>{{rateCalc.tf2.for}} sets for {{rateCalc.tf2.given}} key</p>
          </div>

          <img class="img--trade" src="../assets/tf2key.png" alt="">
        </div>
        <div class="nav--grab">
      
          <div class="text--trade">
            <p class="text--trade-name">CS:GO Keys</p>
            <p>{{rateCalc.csgo.for}} sets for {{rateCalc.csgo.given}} key</p>
          </div>

          <img class="img--trade" src="../assets/csgokey.png" alt="">
        </div>
        <div class="nav--grab">
      
          <div class="text--trade">
            <p class="text--trade-name">Gems</p>
            <p>{{rateCalc.gems.for}} sets for {{rateCalc.gems.given}} gems</p>
          </div>

          <img class="img--trade" src="../assets/gems.png" alt="">

        </div>
