<script>

  import SteamLevel from'steam-level'

  export default {
    name: 'Navbar',
    data() {
      return {
        ssty: {
          backgroundColor: '#a2a2b7'
        },
        rate: {
          tf2: 16,
          csgo: 20,
          gems: 800
        },
        rateCalc: {
          tf2: {
            given: 1,
            for: 16
          },
          csgo: {
            given: 1,
            for: 20,
          },
          gems: {
            given: 21,
            for: 2199
          }
        },
        input: {
          level: 0,
          desired: 0
        }
      }
    },
    methods: {
      delay(ms) {
        return new Promise(res => setTimeout(res, ms));
      },
      async randomColor() {
        await this.delay(2000)
        this.ssty.backgroundColor = '#' + (Math.random() * 0xFFFFFF << 0).toString(16)
        // repeat every 2s and change set a random color
        this.randomColor()
      },
      updateRate() {


          let xp = SteamLevel.getTotalXpFromLevel(this.input.desired) -SteamLevel.getTotalXpFromLevel(this.input.level)
          // console.log(this.input.desired, this.input.level, xp)
          this.rateCalc.tf2.given = ((xp / this.rate.tf2) / 100).toFixed(1)
          this.rateCalc.tf2.for = this.rateCalc.tf2.given * this.rate.tf2

          this.rateCalc.csgo.given = ((xp / this.rate.csgo) / 100).toFixed(1)
          this.rateCalc.csgo.for = this.rateCalc.csgo.given * this.rate.csgo

          this.rateCalc.gems.given = ((xp / 100)  * this.rate.gems).toFixed()
          this.rateCalc.gems.for = xp / 100

      }
    },
    // computed: {
    //   handleLevel(){

    //     if (this.input.desired >= 1) {

    //     }

    //     console.log()
    //     return console.log();
    //     // let cal = this.input.level
    //   }
    // },
    created() {
      this.randomColor()
    }
  }
</script>

